$primary-color: rgb(209, 172, 90);
$secondary-color: rgba(209, 171, 90, 0.242);
$tertiary-color: rgba(46, 30, 116, 0.337);

.home {
  margin-top: -1rem;
  position: relative;

  &__categories {
    width: 100%;
  }
  &__categories-circle {
    width: 7rem;
    height: 100px;
    background-color: $tertiary-color;
    min-width: 100px;
    border-radius: 100%;
  }
  &__categories-circle-img {
    width: 100%;
    display: block;
    border-radius: 100%;
  }
  &__categories-container {
    width: 94vw;
    background-color: $secondary-color;
    border-radius: 10px;
    margin: 1vw 3vw;
    box-sizing: border-box;
    display: flex;
    padding: 1rem 0;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    @media screen and (max-width: 768px) {
      // Estilos específicos para pantallas con un ancho máximo de 768px
      flex-direction: column;
      text-align: center;
    }
  }
  &__categories-container-circle-img {
    width: 100%;
    margin: 2rem;
    height: 60%;
  }
  &__categories-text {
  }
  &__categories-title {
  }
  &__jumbotrom {
    width: 100%;
  }
  &__jumbotrom-container {
    width: 94vw;
    background-color: $primary-color;
    height: 40vh;
    border-radius: 10px;
    margin: 1vw 3vw;
    box-sizing: border-box;
    display: flex;
  }
  &__jumbotrom-img {
    flex:1;
    display: flex;
  }
  &__jumbotrom-sub {
    flex:1;
    display: flex;
    flex-direction: column;
  }
}
